/*
--- 01 TYPOGRAPHY SYSTEM

- Font sizes (px)
10 / 12 / 14 / 16 / 18 / 20 / 24 / 30 / 36 / 44 / 52 / 62 / 74 / 86 / 98

- Font weights
Default: 400
Medium: 500
Semi-bold: 600
Bold: 700

- Line heights
Default: 1
Small: 1.05
Medium: 1.2
Paragraph default: 1.6
Large: 1.8

- Letter spacing
-0.5px
0.75px

--- 02 COLORS

- Primary: ##21243D

--- 03 WHITESPACE

- Spacing system (px)
2 / 4 / 8 / 12 / 16 / 24 / 32 / 48 / 64 / 80 / 96 / 128
*/
/* general style */
@import url("https://fonts.googleapis.com/css2?family=Heebo:wght@400;500;600;700&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  /* 10px / 16px = 0.625 = 62.5% */
  /* Percentage of user's browser font-size setting */
  /* 1 rem = 10px; */
  font-size: 62.5%;
  overflow-x: hidden;
}
body {
  font-family: "Heebo", sans-serif;
  line-height: 1;
  font-weight: 400;
  color: #21243d;
}

.img-responsive {
  max-width: 100%;
  height: auto;
}

.container {
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
  color: #21243d;
}
/* generstartsal ends*/

/* typography starts*/

.heading-primary,
.heading-secondary,
.heading-tertiary {
  /* color: #21243d; */
  font-weight: 700;
}

.heading-primary {
  font-size: 4.4rem;
  line-height: 1.36;
  margin-bottom: 4rem;
}

.heading-secondary {
  font-size: 3rem;
  line-height: 1.2;
  margin-bottom: 2rem;
}

.heading-tertiary {
  font-size: 2.6rem;
  line-height: 1.4;
  margin-bottom: 2rem;
}

.subheading {
  font-size: 2.2rem;
  font-weight: 400;
  color: #21243d;
}
/* typography ends */

/* header section starts */

.navigation-bar {
  display: flex;
  justify-content: space-between;
  /* align-items: center; */
  margin-top: 3rem;
  margin-bottom: 12rem;
}
.main-nav-list {
  list-style: none;
  display: flex;
  align-items: center;
  gap: 4rem;
}

.main-nav-link,
.nav-logo {
  text-decoration: none;
  font-weight: 500;
  font-size: 2rem;
  color: #000;
}

.nav-logo {
  font-size: 2.5rem;
}

/* MOBILE */
.btn-mobile-nav {
  border: none;
  background: none;
  cursor: pointer;
  display: none;
}

.icon-mobile-nav {
  height: 4.8rem;
  width: 4.8rem;
  color: #000;
}

.icon-mobile-nav[name="close-outline"] {
  display: none;
}

.main-nav-link:link,
.main-nav-link:visited {
  color: #000;
  transition: all 0.3s;
}

.main-nav-link:hover,
.main-nav-link:active {
  color: #ff6464;
}
.active {
  color: #ff6464 !important;
}

/* header section ends */

/* hero section starts */

.hero-content {
  display: flex;
  justify-content: space-between;
  margin-top: 9rem;
  margin-bottom: 7rem;
  gap: 30rem;
}

.hero-description {
  font-size: 2rem;
  font-weight: 400;
  line-height: 1.6;
  color: #21243d;
  margin-bottom: 3.8rem;
}

.btn-primary {
  display: inline-block;
  background: #ff6464;
  border-radius: 2px;
  color: #fff;
  font-size: 2rem;
  font-weight: 500;
  text-decoration: none;
  padding: 1.2rem 2rem;
}
.hero-img-box {
  flex-basis: 50%;
}
.hero-img {
  clip-path: circle();
}

/* hero section ends */

/* skill section starts */

.softwares,
.skills {
  display: flex;
  gap: 30px;
}
.softwares {
  margin-top: 7rem;
  margin-bottom: 7.2rem;
}
.skills {
  margin-bottom: 10rem;
}

.softwares h5,
.skills h5 {
  font-family: "Heebo", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  color: #21243d;

  text-decoration: underline;
  flex-basis: 30%;
}

.softwares p,
.skills p {
  font-family: "Heebo", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 1.6rem;
  line-height: 1.6;
  color: #21243d;
}

.softwares p:nth-child(1) {
  margin-bottom: 24px;
}

/* skill section ends */

/* github section starts */
.github-analytics {
  margin-bottom: 4rem;
}
.github-analytics a {
  display: flex;
  gap: 2.4rem;
}

/* github section ends */

/* posts section starts */
.section-posts {
  background-color: #edf7fa;
  padding-block: 3.2rem;
}

.post-heading-container,
.post-card-container {
  display: flex;
  justify-content: space-between;
  margin-bottom: 2.2rem;
}
.post-heading-container a {
  color: #00a8cc;
  font-size: 1.6rem;
}

.post-card-container {
  gap: 2rem;
}

.post-card {
  background-color: #fff;
  border-radius: 4px;
  box-shadow: 0px 4px 10px rgba(187, 225, 250, 0.25);
  padding: 4.4rem;
}
.post-card-date {
  font-size: 1.8rem;
  margin-top: 1.2rem;
  margin-bottom: 1.2rem;
}
.post-card-description,
.work-card-description {
  font-size: 1.6rem;
  line-height: 1.6;
}
/* posts section ends */

/* work section starts */
.section-works {
  padding-block: 3.2rem;
  margin-bottom: 10rem;
}
.work-heading-container {
  margin-bottom: 2.2rem;
}
.work-card {
  /* display: flex;
  gap: 4rem; */
  margin-bottom: 2.4rem;
  border-bottom: 1px solid #e0e0e0;
  padding-block: 1.5rem;
  display: grid;
  grid-template-columns: 50rem auto;
  gap: 3rem;
}
.work-card-date {
  margin-bottom: 2rem;
}
.work-card-year {
  font-size: 1.8rem;
  background-color: #142850;
  color: #fff;
  padding: 0.3rem 1rem;
  border-radius: 1.6rem;
  margin-right: 2rem;
  font-weight: 900;
}

.work-card-category {
  font-size: 2rem;
  color: #8695a4;
  margin-right: 1rem;
}
.work-card-tech-div {
  margin-bottom: 1.4rem;
}

.work-card-tech {
  font-size: 2rem;
  font-weight: 500;
  color: #142850;
  margin-right: 1rem;
}
.work-card-tags {
  font-size: 2rem;
  font-weight: 400;
  line-height: 1.4;
  color: #142850;
}
.work-img-box {
  flex-basis: 50%;
}

.line {
  height: 1px;
  background-color: #e0e0e0;
}
/* work section ends */

/* footer starts */
.footer-content {
  text-align: center;
  margin-bottom: 5.4rem;
}
.social-links {
  list-style: none;
  display: flex;
  gap: 3rem;
  justify-content: center;
  margin-bottom: 2rem;
}

.social-icon {
  font-size: 3.4rem;
  color: #21243d;
}

.copyright {
  font-size: 1.4rem;
}
/* footer ends */

/* blog page css starts*/
.blog {
  margin-bottom: 15rem;
}

.blog .container {
  /* max-width: 100rem;
  margin: 0 auto; */
}

.color-grey {
  color: #8695a4;
}

.blogs .post-card {
  box-shadow: unset;
  padding: 0;
  border-bottom: 1px solid #e0e0e0;
  padding-bottom: 2.4rem;
  margin-bottom: 2.4rem;
}

.blogs .post-card-description {
  max-width: 85ch;
}

/* blog page css ends*/

/* work page css starts */
.work {
  margin-bottom: 15rem;
}
.work .container {
  /* max-width: 100rem;
  margin: 0 auto; */
}

.work .work-card-description {
  max-width: 60ch;
}
/* work page css ends */

/* contact page css start */

.html-form {
  max-width: 60rem;
  margin: 0 auto;
  background: #f1f1f1;
  padding: 4rem;
  margin-bottom: 8rem;
}
input,
textarea {
  width: 100%;
  padding: 13px;
  border-radius: 5px;
  border: none;
  margin-bottom: 20px;
}
button.submit-button {
  border: none;
  background: #1dbf73;
  width: 100%;
  border-radius: 5px;
  padding: 13px;
  font-size: 16px;
  font-weight: 500;
}

/* contact page css start */

/* Media quieres */

/* rem and em do NOT depend on html font-size in media queries! Instead, 1rem = 1em = 16px */

/**************************/
/* BELOW 944px (Tablets) */
/**************************/

@media (max-width: 59em) {
  .container {
    padding: 0 2rem;
  }
  .navigation-bar {
    margin-bottom: 8rem;
  }
  .hero-content {
    gap: 0;
  }
  .section-works {
    margin-bottom: 5rem;
  }
  .github-analytics a {
    flex-direction: column;
    align-items: flex-start;
    gap: 1rem;
  }
  .work-card{
    grid-template-columns: 35rem auto;
  }
}

/**************************/
/* BELOW 544px (Phones) */
/**************************/

@media (max-width: 34em) {
  .heading-primary{
    font-size: 3.6rem;
    line-height: 1.2;
  }
  .heading-secondary {
    font-size: 2.6rem;
    line-height: 1.2;
   
  }
  
  .heading-tertiary {
    font-size: 2.2rem;
    line-height: 1.2;
   
  }
  
  .subheading {
    font-size: 2rem;
    
  }
  .main-nav {
    z-index: 999;
  }

  .navigation-bar {
    align-items: center;
  }

  .hero-content {
    flex-direction: column-reverse;
    gap: 3rem;
  }

  .hero-text-box {
    text-align: center;
  }
  .post-heading-container {
    justify-content: center;
  }
  .post-heading-container a {
    display: none;
  }
  .post-card-container {
    flex-direction: column;
  }
  .work-heading-container {
    text-align: center;
  }
  .work-card {
    /* flex-direction: column; */
    grid-template-columns: 1fr;
    /* text-align: center; */
  }

  /* .work-card:nth-child(2),
  .work-card:nth-child(3) {
    display: none;
  } */

  /* MOBILE NAVIGATION */
  .btn-mobile-nav {
    display: block;
    z-index: 9999;
  }
  .work-img-box img {
    width: 100%;
  }
  .main-nav {
    background-color: rgba(255, 255, 255, 0.9);
    /* -webkit-backdrop-filter: blur(5px);
    backdrop-filter: blur(10px); */
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    transform: translateX(100%);

    display: flex;
    align-items: center;
    justify-content: center;
    transition: all 0.5s ease-in;

    /* Hide navigation */
    /* Allows NO transitions at all */
    /* display: none; */

    /* 1) Hide it visually */
    opacity: 0;

    /* 2) Make it unaccessible to mouse and keyboard */
    pointer-events: none;

    /* 3) Hide it from screen readers */
    visibility: hidden;
  }

  .nav-open .main-nav {
    opacity: 1;
    pointer-events: auto;
    visibility: visible;
    transform: translateX(0);
  }

  .nav-open .icon-mobile-nav[name="close-outline"] {
    display: block;
  }

  .nav-open .icon-mobile-nav[name="menu-outline"] {
    display: none;
  }

  .main-nav-list {
    flex-direction: column;
    gap: 4.8rem;
  }

  .main-nav-link:link,
  .main-nav-link:visited {
    font-size: 3rem;
  }

  .softwares,
  .skills {
    flex-direction: column;
    /* text-align: center; */
  }
  .post-card{
    padding: 3.4rem;
  }

  .github-analytics a img {
    width: 33rem;
    height: auto;
  }
}
